.nav-bar {
    /* background-image: linear-gradient(rgba(255, 255, 255, 0), rgb(41, 41, 41), rgba(255, 255, 255, 0)); */
    background-image: linear-gradient(rgb(41, 41, 41), rgb(41, 41, 41), rgba(255, 255, 255, 0));
}

.nav-bar-name{
    /* text-shadow: 2px 1px red, 0 0 30px rgb(86, 145, 124); */
    text-shadow: 2px 4px rgb(0, 0, 0);
}

.nav-bar-name:hover{
    /* text-shadow: 2px 1px red, 0 0 30px rgb(86, 145, 124); */
    text-shadow: 1px 3px rgb(0, 0, 0), 0 0 8px rgb(255, 255, 255);
}

.nav-bar-link {
  /* text-shadow: 2px 1px red, 0 0 30px rgb(86, 145, 124); */
  text-shadow: -2px 4px rgb(0, 0, 0);
}

.nav-bar-link:hover {
    /* text-shadow: 2px 1px red, 0 0 30px rgb(86, 145, 124); */
    text-shadow: -1px 3px rgb(0, 0, 0), 0 0 8px rgb(255, 255, 255);
}
/* Footer content spacing: set to match navbar positioning */
.footer-container {
    width: 100%;
}

/* Footer links default color - white*/
.foot-link {
    color: inherit;
}

.foot-icon-left{
    /* text-shadow: 2px 1px red, 0 0 30px rgb(86, 145, 124); */
    text-shadow: 3px 3px rgb(0, 0, 0);
}

.foot-icon-left:hover{
    /* text-shadow: 2px 1px red, 0 0 30px rgb(86, 145, 124); */
    text-shadow: 1px 2px rgb(0, 0, 0), 0 0 15px rgb(255, 255, 255);
}

.foot-icon-right{
    /* text-shadow: 2px 1px red, 0 0 30px rgb(86, 145, 124); */
    text-shadow: -3px 3px rgb(0, 0, 0);
}

.foot-icon-right:hover{
    /* text-shadow: 2px 1px red, 0 0 30px rgb(86, 145, 124); */
    text-shadow: -1px 2px rgb(0, 0, 0), 0 0 15px rgb(255, 255, 255);
}
  
/* Footer links color on hover - pale yellow */
.foot-link:hover {
    color: #F1FA8C;
}
  
/* Sticky footer properties */
.footer {
    /* footer gradient */
    /* background-image: linear-gradient(rgba(255, 255, 255, 0), rgb(41, 41, 41), rgba(255, 255, 255, 0)); */

    /* background-color: brown; */
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;

    /* Set the fixed height of the footer here */
    height: 56px;

    /* Vertically center the text there */
    line-height: 55px; 

    /* Text color not active since it is muted */
    color: black;
    text-align: center;
    font-size: 12px;
}

/* Contact popover arrow offset */
.arrow::after {
    left: -4px;
}
.App {
  text-align: center;
}

.page-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);

  /* Page content text color */
  color: #333;
  min-height: 100%;
  min-width: 100%;
  padding: 0;
  margin: 0;
  font-family: 'Saira Semi Condensed', sans-serif;
  font-size: 18px;
  line-height: 150%;
  
  /* Height at which footer collapses; 56px + 30px space between card and footer*/
  margin-bottom: 86px;
}






body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
